<template>
    <div class="article-item">
        <ArticleItem/>
        <!--分页组件-->
        <div class="blog-pagination">
            <el-pagination
                    background
                    :page-sizes="[5, 10, 20]"
                    layout="->, total, prev, pager, next, sizes"
                    :total="total"
                    v-model:page-size="pageSize"
                    v-model:current-page="currentPage"
                    @update:page-size="handleSizeChange"
                    @update:current-page="handleCurrentChange"
            />
        </div>
    </div>
</template>

<script>
import {onMounted, provide, reactive, ref} from "vue";
import axios from "axios";
import Cookies from 'js-cookie'
import ArticleItem from "@/components/ArticleItem.vue";

export default {
    name: "Article",
    components: {ArticleItem},
    setup: function () {
        const baseUrl = 'http://localhost:8000'
        // const baseUrl = 'http://119.91.22.108:8000'

        let articleList = reactive({
            value: [{
                articleId: 0,
                categoryId: 0,
                userId: 0,
                articleTitle: '',
                articleContent: '',
                articlePicture: '',
                deleteFlag: '',
                creationDate: '',
                lastUpdateTime: '',
                categoryName: ''
            }]
        })
        let articleListQuery = reactive({
            pageNum: 1,
            pageSize: 5
        })

        onMounted(() => {
            console.log(111)
            selectArticles()
        })

        let total = ref(0)
        // const token = Cookies.get('token')
        const selectArticles = () => {
            axios.get(
                baseUrl + '/article/selectArticle', {
                    params: articleListQuery,
                    // headers: {
                    //     'token': token
                    // }
                }
            ).then(
                response => {
                    articleList.value = response.data.data.list
                    total.value = response.data.data.total
                    console.log('article', articleList.value)
                }
            )
        }

        //分页功能
        let pageSize = 5
        const handleSizeChange = (value) => {
            articleListQuery.pageSize = value
            pageSize = value
            console.log(pageSize)
            selectArticles()
        }
        let currentPage = 1
        const handleCurrentChange = (value) => {
            articleListQuery.pageNum = value
            currentPage = value
            console.log(currentPage)
            selectArticles()
        }

        provide('articleList', articleList)

        return {
            articleList,
            total,
            handleSizeChange,
            handleCurrentChange,
            pageSize,
            currentPage
        }
    }
}
</script>

<style scoped>
.blog-pagination {
    margin-top: 50px;
    margin-bottom: 100px;
    margin-right: 20vw;
}
</style>